    <div class="from">
      <input
        type="radio"
        value="Online"
        id="Online"
        v-model="paymentType"
        name="paymentType"
        class="paymentType onlineHandler"
        checked
      >
      <input
        type="radio"
        value="delivery"
        id="delivery"
        v-model="paymentType"
        name="paymentType"
        class="paymentType deliveryHandler"
      >
      <div class="row">
        <label class="paymentSelect onlinePayment" for="Online"></label>
        <label class="paymentSelect deliveryPayment" for="delivery"></label>
      </div>
      <div class="onlineArea">
